import Peugeot from "./peg.jpg";
import VW from "./vw.jpg";
import merc from "./merc.jpeg";

function Cars({ salji, salji1, salji2 }) {
  return (
    <div>
      <br />
      <div className="car1">
        <img src={Peugeot} className="p208"></img>
        <h3>Make:{salji.marka}</h3>
        <h3>Model:{salji.model}</h3>
        <h3>Price per day:{salji.cijena}</h3>
        <h3>Number of doors:{salji.broj_vrata}</h3>
        <h3>Gearbox:{salji.gearbox}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="car2">
        <img src={VW} className="vw"></img>
        <h3>Make:{salji1.marka}</h3>
        <h3>Model:{salji1.model}</h3>
        <h3>Price per day:{salji1.cijena}</h3>
        <h3>Number of doors:{salji1.broj_vrata}</h3>
        <h3>Gearbox:{salji1.gearbox}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="car3">
        <img src={merc} className="merc"></img>
        <h3>Make:{salji2.marka}</h3>
        <h3>Model:{salji2.model}</h3>
        <h3>Price per day:{salji2.cijena}</h3>
        <h3>Number of doors:{salji2.broj_vrata}</h3>
        <h3>Gearbox:{salji2.gearbox}</h3>
      </div>
    </div>
  );
}

export default Cars;
