import { Link } from "react-router-dom";

function Nav() {
  return (
    <div>
      <ul>
        <li>
          <Link to="/" className="a">
            Home
          </Link>
        </li>
        <li>
          <a href="https://en.wikipedia.org/wiki/Split,_Croatia">Wikipedia</a>
        </li>
        <li>
          <Link to="/turizam" className="a">
            Tourisam
          </Link>
        </li>
        <li>
          <Link to="/galerija" className="a">
            Transport
          </Link>
        </li>
        <li>
          <Link to="/register" className="a" style={{ float: "right" }}>
            Register
          </Link>
        </li>
        <li className="city">City Tours</li>
      </ul>
    </div>
  );
}

export default Nav;
