import easy from "./plane1.jpg";
import aegean from "./plane2.jpg";
import croatia from "./plane3.jpg";

function Plane({ salji3, salji4, salji5 }) {
  return (
    <div>
      <br />
      <div className="flight1">
        <img src={easy} className="easyjet"></img>
        <h3>From:{salji3.from}</h3>
        <h3>Departure:{salji3.dep}</h3>
        <h3>Arrival:{salji3.ar}</h3>
        <h3>Price per person:{salji3.cijena}</h3>
        <h3>Airline:{salji3.marka}</h3>
        <h3>Plane model:{salji3.model}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="flight2">
        <img src={aegean} className="aegean"></img>
        <h3>From:{salji4.from}</h3>
        <h3>Departure:{salji4.dep}</h3>
        <h3>Arrival:{salji4.ar}</h3>
        <h3>Price per person:{salji4.cijena}</h3>
        <h3>Airline:{salji4.marka}</h3>
        <h3>Plane model:{salji4.model}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="flight3">
        <img src={croatia} className="croatia"></img>
        <h3>From:{salji5.from}</h3>
        <h3>Departure:{salji5.dep}</h3>
        <h3>Arrival:{salji5.ar}</h3>
        <h3>Price per person:{salji5.cijena}</h3>
        <h3>Airline:{salji5.marka}</h3>
        <h3>Plane model:{salji5.model}</h3>
      </div>
    </div>
  );
}

export default Plane;
