import boat1 from "./boat2.jpg";
import boat2 from "./boat3.png";
import boat3 from "./boat4.jpg";

function Boat({ salji6, salji7, salji8 }) {
  return (
    <div>
      <br />
      <div className="jadro1">
        <img src={boat1} className="boat1"></img>
        <h3>From:{salji6.from}</h3>
        <h3>To:{salji6.to}</h3>
        <h3>Departure:{salji6.dep}</h3>
        <h3>Arrival:{salji6.ar}</h3>
        <h3>Company:{salji6.marka}</h3>
        <h3>Price per person:{salji6.cijena}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="jadro2">
        <img src={boat2} className="boat2"></img>
        <h3>From:{salji7.from}</h3>
        <h3>To:{salji7.to}</h3>
        <h3>Departure:{salji7.dep}</h3>
        <h3>Arrival:{salji7.ar}</h3>
        <h3>Company:{salji7.marka}</h3>
        <h3>Price per person:{salji7.cijena}</h3>
        <br />
        <br />
        <div className="border"></div>
      </div>
      <br />
      <br />
      <div className="jadro3">
        <img src={boat3} className="boat3"></img>
        <h3>From:{salji8.from}</h3>
        <h3>To:{salji8.to}</h3>
        <h3>Departure:{salji8.dep}</h3>
        <h3>Arrival:{salji8.ar}</h3>
        <h3>Company:{salji8.marka}</h3>
        <h3>Price per person:{salji8.cijena}</h3>
        <br />
        <br />
      </div>
    </div>
  );
}

export default Boat;
