import dio from "./dio.jpg";
import krka from "./krka1.jpg";
import plit from "./plit1.jpg";
import klis from "./klis.jpg";
import solin from "./solin.jpg";
import brac from "./brac.jpg";

import Nav1 from "./Nav1";
function Turizam() {
  return (
    <div>
      <Nav1 />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <h1 className="t05">
        Must visit places <br />
        in Dalmatia and Split
      </h1>
      <br />
      <br />
      <br />
      <br />
      <div className="dio">
        <h1 className="t06">Diocletian's Palace</h1>
        <div className="dropdown">
          <img src={dio} className="dioimg"></img>
          <div className="dropdown-content">
            <div className="desc">
              Diocletian Palace is one of the best preserved monuments of the
              Roman architecture in the world. The Emperor's Palace was built as
              a combination of a luxury villa - summer house and a Roman
              military camp (castrum), divided into four parts with two main
              streets. Southern part of the Palace was, in this scheme, intended
              for the Emperor's apartment and appropriate governmental and
              religious ceremonies, while the north part was for the Imperial
              guard - the military, servants, storage etc.
            </div>
          </div>
        </div>
      </div>
      <div className="krka">
        <h1 className="t07">National park Krka</h1>
        <div className="dropdown1">
          <img src={krka} className="krkaimg"></img>
          <div className="dropdown-content1">
            <div className="desc1">
              Krka National Park (Croatian: Nacionalni park Krka) is one of the
              Croatian national parks, named after the river Krka (ancient
              Greek: Kyrikos) that it encloses. It is located along the
              middle-lower course of the Krka River in central Dalmatia, in
              Šibenik-Knin county, downstream Miljevci area, and just a few
              kilometers northeast of the city of Šibenik.[1] It was formed to
              protect the Krka River and is intended primarily for scientific,
              cultural, educational, recreational, and tourism activities. It is
              the seventh national park in Croatia and was proclaimed a national
              park in 1985.
            </div>
          </div>
        </div>
      </div>
      <div className="plit">
        <h1 className="t08">Plitvice lakes</h1>
        <div className="dropdown2">
          <img src={plit} className="plitimg"></img>
          <div className="dropdown-content2">
            <div className="desc2">
              Plitvice Lakes National Park (Croatian: Nacionalni park Plitvička
              jezera, colloquially Plitvice, pronounced [plîtʋitse]) is one of
              the oldest and largest national parks in Croatia. In 1979,
              Plitvice Lakes National Park was inscribed on the UNESCO World
              Heritage list, due to its outstanding and picturesque series of
              tufa lakes, caves, connected by waterfalls.
            </div>
          </div>
        </div>
      </div>
      <div className="klis">
        <h1 className="t08">Klis fortress</h1>
        <div className="dropdown3">
          <img src={klis} className="klisimg"></img>
          <div className="dropdown-content3">
            <div className="desc3">
              The Klis Fortress (Croatian: Tvrđava Klis) is a medieval fortress
              situated above a village bearing the same name, near Split,
              Croatia. From its origin as a small stronghold built by the
              ancient Illyrian tribe Dalmatae, becoming a royal castle that was
              the seat of many Croatian kings, to its final development as a
              large fortress during the Ottoman wars in Europe.
            </div>
          </div>
        </div>
      </div>
      <div className="solin">
        <h1 className="t09">Salona ruins</h1>
        <div className="dropdown4">
          <img src={solin} className="solinimg"></img>
          <div className="dropdown-content4">
            <div className="desc4">
              Salona (Ancient Greek: Σάλωνα) was an ancient city and the capital
              of the Roman province of Dalmatia.[1] Salona is located in the
              modern town of Solin, next to Split, in Croatia. Salona was
              founded in the 3rd century BC and was mostly destroyed in the
              invasions of the Avars and Slavs in the seventh century AD. Many
              Roman characteristics can be seen such as walls; a forum; a
              theatre; an amphitheatre, public baths and an aqueduct.
            </div>
          </div>
        </div>
      </div>
      <div className="brac">
        <h1 className="t10">Island of Brač</h1>
        <div className="dropdown5">
          <img src={brac} className="bracimg"></img>
          <div className="dropdown-content5">
            <div className="desc5">
              rač (pronounced [brâːtʃ]; local Chakavian: Broč, pronounced
              [broːtʃ]; Latin: Bretia, Brattia; Italian: Brazza) is an island in
              the Adriatic Sea within Croatia, with an area of 396 square
              kilometres (153 sq mi),[1] making it the largest island in
              Dalmatia, and the third largest in the Adriatic. It is separated
              from the mainland by the Brač Channel, which is 5 to 13 km (3 to 8
              mi) wide.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Turizam;
