import face from "./fb.png";
import twitter from "./tw.png";
import inst from "./inst.jpg";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <div className="footer">
      <div className="icons">
        <h3 className="l">Links</h3>
        <a href="https://www.facebook.com/touristboardsplit?fref=ts">
          <img src={face} alt="fb" className="fb"></img>
        </a>
        <a href="https://twitter.com/SplitCityInfo">
          <img src={twitter} alt="tw" className="tw"></img>
        </a>
        <a href="https://www.instagram.com/visitsplit/">
          <img src={inst} alt="tw" className="inst"></img>
        </a>
        <h2 className="copy">
          &copy;2022 City Tours d.o.o. - All rights reserved
        </h2>
      </div>
      <h3 className="ime">Lovre Hirons g2p</h3>
    </div>
  );
}

export default Footer;
