import Nav from "./Navbar";
import Split from "./split.jpg";
import Cards from "./cards";
import Footer from "./Footer";
function Home() {
  return (
    <div>
      <img src={Split} className="img1"></img>
      <h1 className="animate-charcter">
        Split
        <br />
        The heart of Croatia
      </h1>
      <Nav />
      <br />
      <br />
      <Cards />
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d46293.5305127497!2d16.449083500000004!3d43.5159842!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x13355dfc6bbcf517%3A0xa1798ff631b49f98!2s21000%2C%20Split!5e0!3m2!1sen!2shr!4v1653406972255!5m2!1sen!2shr"
        allowfullscreen=""
        loading="lazy"
        referrerpolicy="no-referrer-when-downgrade"
        className="map"
      ></iframe>
      <br />
      <br />
      <br />
      <br />
      <br />

      <Footer />
    </div>
  );
}

export default Home;
