import Nav from "./Navbar";
import regim from "./regimg.jpg";
function Register() {
  return (
    <div>
      <img src={regim} className="regimg"></img>
      <Nav />
      <div className="forms">
        <form>
          <h1>Sign in</h1>
          <label>
            Name:
            <input type="text" name="name" placeholder="FirstName" />
          </label>
          <br />
          <br />
          <br />
          <label>
            LastName:
            <input type="text" name="surname" placeholder="LastName" />
          </label>
          <br />
          <br />
          <br />
          <label>
            Email:
            <input type="email" name="email" placeholder="email"></input>
          </label>
          <br />
          <br />
          <br />
          <label>
            Password:
            <input
              type="password"
              name="password"
              placeholder="password"
            ></input>
          </label>
          <br />
          <br />

          <button>Submit</button>
        </form>
      </div>
    </div>
  );
}

export default Register;
