import Opel from "./opel.jpg";
import { Link } from "react-router-dom";
import Boat from "./boat.jpg";
import Plane from "./plane.jpg";

function Cards() {
  return (
    <div className="offers">
      <h1 className="trans">Transport</h1>
      <div className="card1">
        <img src={Opel} alt="car" className="opel"></img>
        <h2>Rent a car</h2>
        <Link to="/galerija" className="linkoffer">
          <a>Click to see our offers</a>
        </Link>
      </div>
      <div className="card2">
        <img src={Boat} alt="boat" className="jadro"></img>
        <h2>Boat cruise</h2>
        <Link to="/galerija" className="linkoffer">
          <a>Click to see our offers</a>
        </Link>
      </div>
      <div className="card3">
        <img src={Plane} alt="plane" className="plane"></img>
        <h2>Flights</h2>
        <Link to="/galerija" className="linkoffer">
          <a>Click to see our offers</a>
        </Link>
      </div>
    </div>
  );
}

export default Cards;
